<template>
    <div class="ManualRecord">
        <sHeader name="手动录入"/>
        <div>
            <van-field
                    v-model="sleepTime"
                    is-link
                    readonly
                    name="datetimePicker"
                    label="入睡时间"
                    placeholder="请选择入睡时间"
                    @click="showSleepTimePicker = true"
                    :min-date="minDate"
                    :max-date="maxDate"
            />
            <van-popup v-model:show="showSleepTimePicker" position="bottom">
                <van-datetime-picker
                        v-model="currentDate"
                        type="datetime"
                        :formatter="formatter"
                        title="选择入睡时间"
                        @confirm="confirm1"
                        @cancel="showSleepTimePicker=false"
                        :min-date="minDate"
                        :max-date="maxDate"
                />
            </van-popup>
            <van-field
                    v-model="getUpTime"
                    is-link
                    readonly
                    name="datetimePicker"
                    label="起床时间"
                    placeholder="请选择起床时间"
                    @click="showGetUpTimePicker = true"
            />
            <van-popup v-model:show="showGetUpTimePicker" position="bottom">
                <van-datetime-picker
                        v-model="currentDate"
                        type="datetime"
                        :formatter="formatter"
                        title="选择起床时间"
                        @confirm="confirm2"
                        @cancel="showGetUpTimePicker=false"
                        :min-date="minDate"
                        :max-date="maxDate"
                />
            </van-popup>
        </div>
        <div class="footer" @click="mmanualInput">
            <span class="btn-submit-text">保存</span>
        </div>
    </div>
</template>
<script>
  import sHeader from '@/components/SimpleHeader';
  import {reactive, onMounted, toRefs, ref} from 'vue';
  import {useStore} from "vuex";
  import {useRouter} from 'vue-router';
  import moment from "moment";
  import {manualInput} from '../../../service/sleep'
  import {Toast} from 'vant'

  export default {
    name: "ManualRecord",
    components: {
      sHeader
    },
    props: {},
    setup() {
      const store = useStore();
      const router = useRouter();
      const goTo = (r, query) => {
        router.push({path: r, query: query || {}})
      }
      onMounted(() => {

      })
      const state = reactive({
        sleepTime: moment().format('YYYY-MM-DD HH:mm'),
        showSleepTimePicker: false,
        showGetUpTimePicker: false,
        getUpTime: moment().format('YYYY-MM-DD HH:mm'),
        minDate: new Date(1910, 0, 1),
        maxDate: new Date(),
        currentDate: new Date(),
      })
      const formatter = (type, val) => {
        if (type === 'year') {
          return `${val}年`
        }
        if (type === 'month') {
          return `${val}月`;
        }
        if (type === 'day') {
          return `${val}日`;
        }
        if (type === 'hour') {
          return `${val}时`
        }
        if (type === 'minute') {
          return `${val}分`
        }
        return val;
      };
      const confirm1 = (value) => {
        state.showSleepTimePicker = false;
        state.sleepTime = moment(new Date(value)).format('YYYY-MM-DD HH:mm')
      }
      const confirm2 = (value) => {
        state.showGetUpTimePicker = false;
        state.getUpTime = moment(new Date(value)).format('YYYY-MM-DD HH:mm')
      }
      const mmanualInput = async () => {
        if (!state.sleepTime) {
          Toast('请选择入睡时间')
          return
        }
        if (!state.getUpTime) {
          Toast('请选择起床时间')
          return
        }
        const params = {
          "startTime": state.sleepTime,
          "endTime": state.getUpTime,
          "sleepType": 0,
          // "startTimeDesc":state.sleepTime,
          // "endTimeDesc":state.getUpTime,
          "userId": "352997133139443712",
          // "start":state.sleepTime,
          // "end":state.getUpTime

        }
        const res = await manualInput(params)
        if (res.code == 200) {
          router.go(-1)
        }
      }

      return {
        ...toRefs(state),
        formatter,
        confirm1,
        confirm2,
        mmanualInput,
      }
    }
  }
</script>

<style scoped lang="less">
    @import '../../../common/style/mixin';

    .ManualRecord {
        height: 100%;
        background: #f5f6f9;
    }
</style>
